@charset 'utf-8';

@import '../styles/_common.scss';

.preset-list__container {
  margin-bottom: 30px;

  .preset-list__title {
    font-size: 16px;
    color: $theme-color-title;
    line-height: 2;
    margin-bottom: 10px;
    position: relative;
    padding-left: 15px;

    &::before {
      content: '';
      position: absolute;
      top: 50%;
      left: 0;
      transform: translate(-0, -50%);
      width: 4px;
      height: 16px;
      background-color: $theme-color-title;
    }
  }

  .preset-list__content {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
}
