* {
  box-sizing: border-box; }

html,
body {
  margin: 0;
  padding: 0; }

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
  font-weight: 400; }

li {
  list-style: none; }

ul,
ol {
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0; }

a {
  text-decoration: none; }
  a:focus, a:active, a:visited {
    text-decoration: none; }

button {
  -webkit-appearance: none;
  -moz-appearance: none; }

table {
  border-collapse: collapse; }

@keyframes rotate {
  from {
    transform: rotate(0); }
  to {
    transform: rotate(360deg); } }

* {
  font-family: -apple-system, BlinkMacSystemFont, "PingFang SC", "Hiragino Sans GB", "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Fira Sans", "Open Sans", "Droid Sans", "Microsoft YaHei", "WenQuanYi Micro Hei", "Helvetica Neue", Tahoma, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  -webkit-overflow-scrolling: touch;
  scroll-behavior: smooth;
  box-sizing: border-box; }
  ::-webkit-scrollbar {
    width: 0;
    overflow: auto; }
  ::-webkit-scrollbar-track {
    background-color: transparent;
    border-radius: 10px;
    transition: background-color 0.1s; }
  ::-webkit-scrollbar-track-piece {
    background-color: transparent;
    border-radius: 10px;
    transition: background-color 0.1s; }
  ::-webkit-scrollbar-thumb {
    background-color: rgba(0, 0, 0, 0.3);
    border-radius: 10px;
    transition: background-color 0.1s; }
  *:focus {
    outline: none; }
  *::before, *::after {
    box-sizing: border-box; }

.scroll-lock {
  overflow: hidden; }

[disabled] {
  cursor: not-allowed !important; }

[readonly] {
  cursor: default !important; }

.clickable {
  cursor: pointer !important; }

img {
  border: none; }

h1,
h2,
h3,
h4,
h5,
h6,
p,
a,
span,
div,
th,
td {
  color: #999; }

input:-webkit-autofill,
textarea:-webkit-autofill,
select:-webkit-autofill {
  background-color: transparent;
  background-image: none;
  box-shadow: 0 0 0 1000px #fff inset;
  color: #fff;
  -webkit-text-fill-color: #fff; }

input::placeholder,
textarea::placeholder,
select::placeholder {
  color: #cfcfcf; }

.form-group {
  margin-bottom: 15px;
  position: relative; }
  .form-group:last-of-type {
    margin-bottom: 0; }

#root {
  height: 100%;
  position: relative; }
